<template>
  <view class="uni_audio_controller" @click="toHome">
    <image src="~@/static/record.png"
           class="audio_img"
           :class="[getIsPlay ? 'audio_anim_play' : 'audio_anim_pause']">
    </image>
    <text class="playTime"
          :class="[getIsPlay ? 'active' : '']">
      {{setDuration}}
    </text>
    <view class="control" @click.stop="onPlay">
      <text class="iconfont"
            :class="[getIsPlay ? 'icon-audioPause' : 'icon-audioPlay']">
      </text>
    </view>
  </view>
</template>

<script>
import {mapMutations, mapState} from 'vuex';
import types from '@/store/mutations-types';

export default {
  data() {
    return {
    }
  },
  computed: {
    ...mapState({
      getAudio: 'audio',
      getIsPlay: 'isPlay',
      getDuration: 'duration',
      getCurrentTime: 'currentTime'
    }),
    setDuration() {
      let t = this.getCurrentTime,
          m = 0, s = 0;
      if (!t) return '00:00';
      m = Math.floor(t / 60) + '';
      s = Math.floor(t % 60) + '';
      m.length === 1 && (m = '0' + m);
      s.length === 1 && (s = '0' + s);
      return m + ':' + s;
    }
  },
  methods: {
    ...mapMutations([
        types.SET_SWITCH,
        types.SET_CURRENT_PAGE
    ]),

    onPlay() {
      this.getIsPlay ? this.getAudio.pause() : this.getAudio.play();
    },

    toHome() {
      uni.navigateTo({
        url: '/pages/index/Index'
      });
      this[types.SET_CURRENT_PAGE](0);
    },
  },
}
</script>

<style scoped lang="scss">
  .uni_audio_controller{
    @include wh(50%, 110rpx);
    background-color: $bgc-black1;
    border-radius: 60rpx;
    position: fixed;
    bottom: 2%;
    left: 6%;
    z-index: 1000;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .audio_img{
      @include wh(130rpx, 130rpx);

    }

    .playTime{
      color: $font-color-grey;
      font-size: 48rpx;
      font-weight: bold;
      padding-left: 20rpx;
    }
    .active{
      color: $font-color-white;
    }

    .audio_anim_play{
      animation: audioPlay 8s linear infinite;
      animation-play-state: running;
    }
    .audio_anim_pause{
      animation: audioPlay 8s linear infinite;
      animation-play-state: paused;
    }

    .control{
      @include wh(100rpx, 100%);
      position: relative;
      .icon-audioPlay{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate3d(-50%, -50%, 0);
        font-size: 42rpx;
        color: $font-color-white;
      }
      .icon-audioPause{
        @extend .icon-audioPlay;
        color: $font-color-grey;
      }
    }
  }

  @keyframes audioPlay {
    0%{
      transform: rotate(0);
    }
    100%{
      transform: rotate(360deg);
    }
  }
</style>